.grid-item {
  display: block;
  text-align: center;
  position: relative;
}

.grid-item .svg {
  opacity: 0;
  position: absolute;
  bottom: 55%;
  left: 50%;
  transform: translate(-50%, 0);
  transition: 0.5s;
  width: 98%;
  padding: 10px 5px;
  @media screen and (min-width: 768px) {
    bottom: 60%;
  }
}
.grid-item:hover .svg {
  opacity: 1;
}

.svg {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
